import React from "react";
import Button from "@material-ui/core/Button";
import Grid from "@material-ui/core/Grid";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
    form: {
        display: "flex",
        flexDirection: "row"
    }
}));
export default function DoranOperationPanel(props) {
    const classes = useStyles();
    const { children, header, footer, size = 12, validate } = props;
    const { onSave, saveText } = props;

    const handleSubmit = (event) => {
        event.preventDefault();
    };

    return (
        <React.Fragment>
            {header}
            <form className={classes.form} autoComplete='off' noValidate onSubmit={handleSubmit}>
                <Grid container spacing={1}>
                    {children}
                    {onSave && (
                        <Grid item sm={size}>
                            <Button
                                type='submit'
                                variant='contained'
                                color='primary'
                                fullWidth={true}
                                onClick={onSave}
                                disabled={!!validate()}
                            >
                                {saveText}
                            </Button>
                        </Grid>
                    )}
                    {footer}
                </Grid>
            </form>
        </React.Fragment>
    );
}
