import React from "react";
import PropTypes from "prop-types";
import Grid from "@material-ui/core/Grid";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import Alert from "@material-ui/lab/Alert";
import { makeStyles } from "@material-ui/core/styles";
import DoranContactInput from "./doran-contact-input";

const useStyles = makeStyles((theme) => ({
    section: {
        marginTop: theme.spacing(2)
    },
    titleBox: {
        margin: "auto 0"
    },
    titleLabel: {
        fontSize: "0.9rem"
    },
    addBox: {
        textAlign: "right"
    },
    addButton: {
        fontSize: "0.7rem",
        color: "green"
    },
    alert: {
        width: "100%",
        border: "1px solid lightgrey"
    }
}));
export default function DoranContact(props) {
    const classes = useStyles();
    const { contactTypes, value, onChange } = props;
    const { title, addText, inputLabelText, selectLabelText, emptyText } = props;

    const handleAdd = () => {
        value.push({ type: contactTypes[0], value: "" });
        onChange(value);
    };

    const handleRemove = (index) => {
        value.splice(index, 1);
        onChange(value);
    };

    const handleChange = (index, newValue) => {
        value[index] = newValue;
        onChange(value);
    };

    const renderContactInput = (index) => {
        const attributes = {
            itemKey: index,
            value: value[index],
            selectName: `select-${index}`,
            selectLabel: selectLabelText,
            options: contactTypes,
            inputName: `input-${index}`,
            inputLabel: inputLabelText,
            onChange: handleChange,
            onRemove: handleRemove
        };

        return <DoranContactInput {...attributes} />;
    };

    return (
        <Grid item sm={12}>
            <Grid container spacing={1} className={classes.section}>
                <Grid item sm={10} className={classes.titleBox}>
                    <Typography variant='h6' className={classes.titleLabel}>
                        {title}
                    </Typography>
                </Grid>
                <Grid item sm={2} className={classes.addBox}>
                    <Button size='small' onClick={() => handleAdd()} className={classes.addButton}>
                        {addText}
                    </Button>
                </Grid>
                {value.length > 0 &&
                    value.map((_, index) => (
                        <Grid item sm={12} key={index}>
                            {renderContactInput(index)}
                        </Grid>
                    ))}
                {!value.length && (
                    <Alert severity='info' className={classes.alert}>
                        {emptyText}
                    </Alert>
                )}
            </Grid>
        </Grid>
    );
}

DoranContact.propTypes = {
    contactTypes: PropTypes.array.isRequired,
    value: PropTypes.array.isRequired,
    onChange: PropTypes.func,
    title: PropTypes.string.isRequired,
    addText: PropTypes.string.isRequired,
    inputLabelText: PropTypes.string.isRequired,
    selectLabelText: PropTypes.string.isRequired,
    emptyText: PropTypes.string
};
