import React from "react";
import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogActions from "@material-ui/core/DialogActions";
import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
    title: {
        fontSize: theme.typography.pxToRem(16),
        margin: 0
    },
    content: {
        fontSize: theme.typography.pxToRem(13)
    },
    button: {
        fontSize: theme.typography.pxToRem(12)
    }
}));
function DoranRemoveDialog(props) {
    const classes = useStyles();
    const { titleText, contentText, disagreeText, agreeText } = props;
    const { data, onClose, onDelete } = props;
    const [open, setOpen] = React.useState(props.open);

    const handleClose = () => {
        setOpen(false);
        onClose();
    };

    return (
        <Dialog open={open} onClose={handleClose}>
            <DialogTitle disableTypography={false}>{titleText}</DialogTitle>
            <DialogContent>
                <DialogContentText className={classes.content}>{contentText}</DialogContentText>
            </DialogContent>
            <DialogActions>
                <Button onClick={handleClose} color='primary' className={classes.button}>
                    {disagreeText}
                </Button>
                <Button onClick={() => onDelete(data)} color='primary' className={classes.button}>
                    {agreeText}
                </Button>
            </DialogActions>
        </Dialog>
    );
}

export default DoranRemoveDialog;
