import React from "react";
import throttle from "lodash/throttle";
import TextField from "@material-ui/core/TextField";
import Autocomplete from "@material-ui/lab/Autocomplete";
import CircularProgress from "@material-ui/core/CircularProgress";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
    root: {
        "& > * + *": {
            marginTop: theme.spacing(3)
        }
    },
    input: {
        fontSize: "0.9rem"
    }
}));

export default function DoranMultiSelectAutocomplete(props) {
    const classes = useStyles();

    const { label, name, value, disabled, queryName } = props;
    const { serviceCallback, convertCallback, onChangeCallback, defaultCallbackObject } = props;

    const [inputValue, setInputValue] = React.useState(null);
    const [loading, setLoading] = React.useState(false);
    const [options, setOptions] = React.useState([]);

    const fetch = React.useMemo(() => {
        return throttle(async ({ input }, callback) => {
            let searchObject = { [queryName]: input };

            if (defaultCallbackObject) {
                searchObject = Object.assign(defaultCallbackObject, { [queryName]: input });
            }

            const data = await serviceCallback(searchObject);
            const items = convertCallback(data);
            callback(items);
        }, 200);
    }, [queryName, serviceCallback, convertCallback, defaultCallbackObject]);

    React.useEffect(() => {
        let active = true;

        if (!inputValue) {
            setOptions([]);
            return;
        }

        fetch({ input: inputValue }, (results) => {
            if (active) {
                const ids = value.map((e) => e.value);
                const items = results.filter((e) => !ids.includes(e.value));
                setOptions(items || []);
            }
        });

        return () => {
            active = false;
        };
    }, [value, inputValue, fetch]);

    const inputAttrs = { label, disabled };
    const renderInput = (params) => {
        return (
            <TextField
                {...params}
                {...inputAttrs}
                fullWidth
                variant='outlined'
                InputLabelProps={{ className: classes.input }}
                onChange={(event) => setInputValue(event.target.value)}
                InputProps={{
                    ...params.InputProps,
                    className: classes.input,
                    endAdornment: (
                        <React.Fragment>
                            {loading ? <CircularProgress color='inherit' size={20} /> : null}
                            {loading || params.InputProps.endAdornment}
                        </React.Fragment>
                    )
                }}
            />
        );
    };

    const filterOptions = (options) => {
        return options.filter((e) => !e.disabled);
    };

    return (
        <div className={classes.root}>
            <Autocomplete
                multiple
                freeSolo
                value={value}
                options={options}
                getOptionLabel={(option) => option.label}
                filterSelectedOptions
                renderInput={renderInput}
                filterOptions={filterOptions}
                onOpen={() => setLoading(true)}
                onClose={() => setLoading(false)}
                onChange={(event, newValue) => onChangeCallback(event, name, newValue)}
            />
        </div>
    );
}
