import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogActions from "@material-ui/core/DialogActions";
import Button from "@material-ui/core/Button";

const useStyles = makeStyles((theme) => ({
    title: {
        fontSize: theme.typography.pxToRem(16),
        margin: 0
    },
    content: {
        fontSize: theme.typography.pxToRem(13)
    },
    button: {
        fontSize: theme.typography.pxToRem(13)
    }
}));

function DoranConfirmDialog(props) {
    const classes = useStyles();
    const { data, onClose, onDelete } = props;
    const { title, content, agreeText, disagreeText } = props;
    const [open, setOpen] = React.useState(props.open);

    const handleClose = () => {
        setOpen(false);
        onClose();
    };

    return (
        <div>
            <Dialog open={open} onClose={handleClose}>
                <DialogTitle className={classes.title}>{title}</DialogTitle>
                <DialogContent>
                    <DialogContentText className={classes.content}>{content}</DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleClose} color='primary' className={classes.button}>
                        {disagreeText}
                    </Button>
                    <Button onClick={() => onDelete(data)} color='primary' className={classes.button}>
                        {agreeText}
                    </Button>
                </DialogActions>
            </Dialog>
        </div>
    );
}

export default DoranConfirmDialog;
