import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import Paper from "@material-ui/core/Paper";
import DoranPagination from "./pagination/doran-pagination";
import DoranHeader from "./table/doran-table-header";
import DoranBody from "./table/doran-table-body";

const styles = makeStyles((theme) => ({
    root: {
        width: "100%",
        marginTop: theme.spacing(3)
    },
    table: {
        minWidth: 500
    },
    tableWrapper: {
        overflowX: "auto"
    }
}));
export default function DoranPaginationTable(props) {
    const classes = styles();

    const { columns, items, total, pageNumber, pageSize, pageSizeSelectable, keys, showPagination = true } = props;
    const { onPageSizeChange, onPageNumberChange } = props;
    const { rowsPerPageLabel, displayedRowsFormatter } = props;

    return (
        <Paper className={classes.root}>
            <div className={classes.tableWrapper}>
                <Table className={classes.table} size='small'>
                    <DoranHeader columns={columns} />
                    <DoranBody items={items} columns={columns} keys={keys} />
                    {showPagination && (
                        <DoranPagination
                            colSpan={columns.length}
                            totalCount={total}
                            pageSizeSelectable={pageSizeSelectable}
                            pageSize={pageSize}
                            pageNumber={pageNumber}
                            displayedRowsFormatter={displayedRowsFormatter}
                            rowsPerPageLabel={rowsPerPageLabel}
                            onChangePageClick={(event, newPage) => onPageNumberChange(newPage)}
                            onChangeRowsPerPageClick={(event) => onPageSizeChange(parseInt(event.target.value, 10))}
                        />
                    )}
                </Table>
            </div>
        </Paper>
    );
}
