import React from "react";
import Checkbox from "@material-ui/core/Checkbox";
import Typography from "@material-ui/core/Typography";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import { makeStyles } from "@material-ui/core/styles";

function CustomCheckbox(props) {
    const { name, value, onChange, disabled } = props;

    const attributes = {
        name,
        value,
        checked: value,
        onChange,
        disabled
    };

    return <Checkbox color='primary' {...attributes} />;
}

const useStyles = makeStyles(() => ({
    label: {
        fontSize: "0.9rem"
    }
}));
export default function DoranCheckbox(props) {
    const { label, name, placement, error, ...rest } = props;
    const classes = useStyles();

    return (
        <FormControlLabel
            control={<CustomCheckbox name={name} {...rest} />}
            label={<Typography className={classes.label}>{label}</Typography>}
            labelPlacement={placement}
        />
    );
}
