import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import Card from "@material-ui/core/Card";
import CardMedia from "@material-ui/core/CardMedia";
import CloudUploadIcon from "@material-ui/icons/CloudUpload";
import DeleteForeverIcon from "@material-ui/icons/DeleteForever";
import { fileToBase64 } from "../../helpers";

const useStyles = makeStyles((theme) => ({
    root: {
        height: "100%"
    },
    input: {
        display: "none"
    },
    card: {
        height: "calc(100% - 72px)",
        marginTop: theme.spacing(1)
    },
    media: {
        height: "100%",
        width: "100%"
    }
}));
export default function DoranImageInput(data) {
    const { name, value, emptyImage, onChange, removeText, uploadText, minHeight } = data;
    const [image, setImage] = React.useState(value);

    const classes = useStyles();

    const handleChange = async ({ target }) => {
        if (target.files.length > 0) {
            const image = await fileToBase64(target.files[0]);
            setImage(image);
            onChange(name, image);
        }
    };

    const handleRemove = () => {
        setImage(null);
        onChange(name, null);
    };

    return (
        <div className={classes.root}>
            {image ? (
                <Button
                    variant='contained'
                    component='span'
                    fullWidth
                    color='secondary'
                    startIcon={<DeleteForeverIcon />}
                    onClick={handleRemove}
                >
                    {removeText}
                </Button>
            ) : (
                <React.Fragment>
                    <input id={name} type='file' accept='image/*' className={classes.input} onChange={handleChange} />
                    <label htmlFor={name}>
                        <Button variant='outlined' component='span' fullWidth startIcon={<CloudUploadIcon />}>
                            {uploadText}
                        </Button>
                    </label>
                </React.Fragment>
            )}

            <Card className={classes.card} style={{ height: minHeight, minHeight }}>
                {value && value.base64 ? (
                    <CardMedia className={classes.media} image={value.base64} />
                ) : (
                    <CardMedia className={classes.media} image={emptyImage} />
                )}
            </Card>
        </div>
    );
}
