/* global FileReader, File, fetch */

export function fileToBase64(file) {
    return new Promise((resolve) => {
        const { type, name } = file;
        const extension = name.split(".").pop().toLowerCase();

        const fileReader = new FileReader();
        fileReader.readAsDataURL(file);
        fileReader.onload = (file) => {
            const base64 = file.target.result;
            const base = base64.replace(/^data:[/a-z0-9]+;base64,/, "");
            resolve({ name, type, extension, base, base64 });
        };
    });
}

export function base64ToFile(data) {
    const { name, type, base64 } = data;
    return new Promise((resolve) => {
        fetch(base64)
            .then((res) => res.blob())
            .then((blob) => {
                resolve(new File([blob], name, { type }));
            });
    });
}

export function urlToBase64(url) {
    return new Promise((resolve) => {
        fetch(url)
            .then((res) => res.blob())
            .then((blob) => {
                const name = url.split("/").pop().toLowerCase();

                const extension = name.split(".").pop().toLowerCase();

                const fileReader = new FileReader();
                fileReader.readAsDataURL(blob);
                fileReader.onload = (file) => {
                    const base64 = file.target.result;
                    const base = base64.replace(/^data:[/a-z0-9]+;base64,/, "");
                    resolve({ name, type: blob.type, extension, base, base64 });
                };
            });
    });
}

export function urlToBase64Post(url, data) {
    return new Promise((resolve) => {
        fetch(url, {
            method: "post",
            body: JSON.stringify(data),
            headers: { "Content-Type": "application/json" }
        })
            .then((res) => res.blob())
            .then((blob) => {
                const name = url.split("/").pop().toLowerCase();

                const extension = name.split(".").pop().toLowerCase();

                const fileReader = new FileReader();
                fileReader.readAsDataURL(blob);
                fileReader.onload = (file) => {
                    const base64 = file.target.result;
                    const base = base64.replace(/^data:[/a-z0-9]+;base64,/, "");
                    resolve({ name, type: blob.type, extension, base, base64 });
                };
            });
    });
}

export default { fileToBase64, base64ToFile, urlToBase64, urlToBase64Post };
