import React from "react";
import Grid from "@material-ui/core/Grid";
import Button from "@material-ui/core/Button";
import ButtonGroup from "@material-ui/core/ButtonGroup";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import ClickAwayListener from "@material-ui/core/ClickAwayListener";
import Grow from "@material-ui/core/Grow";
import Paper from "@material-ui/core/Paper";
import Popper from "@material-ui/core/Popper";

export default function DoranSplitButton(props) {
    const { title, color, children } = props;

    const [open, setOpen] = React.useState(false);
    const anchorRef = React.useRef(null);

    const handleToggle = () => {
        setOpen((prevOpen) => !prevOpen);
    };

    const handleClose = (event) => {
        if (anchorRef.current && anchorRef.current.contains(event.target)) {
            return;
        }

        setOpen(false);
    };

    const arrowStyle = (placement) => {
        return { transformOrigin: placement === "bottom" ? "center top" : "center bottom" };
    };

    const containerAttrs = {
        direction: "column",
        alignItems: "center"
    };

    const buttonAttrs = {
        color: color,
        size: "small",
        "aria-controls": open ? "split-button-menu" : undefined,
        "aria-expanded": open ? "true" : undefined,
        "aria-haspopup": "menu",
        onClick: handleToggle
    };

    const menuAttrs = {
        open: open,
        anchorEl: anchorRef.current,
        role: undefined,
        style: { zIndex: 99 }
    };

    return (
        <Grid container {...containerAttrs}>
            <Grid item xs={12}>
                <ButtonGroup variant='contained' color={color} ref={anchorRef}>
                    <Button>{title}</Button>
                    <Button {...buttonAttrs}>
                        <ExpandMoreIcon />
                    </Button>
                </ButtonGroup>
                <Popper transition disablePortal {...menuAttrs}>
                    {({ TransitionProps, placement }) => (
                        <Grow {...TransitionProps} style={arrowStyle(placement)}>
                            <Paper>
                                <ClickAwayListener onClickAway={handleClose}>{children}</ClickAwayListener>
                            </Paper>
                        </Grow>
                    )}
                </Popper>
            </Grid>
        </Grid>
    );
}
