import React from "react";
import FormControl from "@material-ui/core/FormControl";
import InputLabel from "@material-ui/core/InputLabel";
import FormHelperText from "@material-ui/core/FormHelperText";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import ListItemText from "@material-ui/core/ListItemText";
import Checkbox from "@material-ui/core/Checkbox";
import KeyboardArrowDownIcon from "@material-ui/icons/KeyboardArrowDown";
import { makeStyles, withStyles } from "@material-ui/core/styles";

const CustomizedSelect = withStyles({
    root: {
        fontSize: "0.9rem",
        lineHeight: "1.275em"
    }
})(Select);

const CustomizedListItemText = withStyles({
    root: {
        fontSize: "0.9rem"
    }
})(ListItemText);

const CustomizedMenuItem = withStyles({
    root: {
        padding: "0px"
    }
})(MenuItem);

const useStyles = makeStyles(() => ({
    label: {
        fontSize: "0.9rem"
    }
}));
export default function DoranMultiTagSelect(props) {
    const { label, name, value, options, onChange, error, ...rest } = props;
    const classes = useStyles();

    const inputLabel = React.useRef(null);
    const [labelWidth, setLabelWidth] = React.useState(0);
    React.useEffect(() => {
        setLabelWidth(inputLabel.current.offsetWidth);
    }, []);

    const attributes = {
        value,
        onChange: (event, child) => onChange(event, name, child.props.value),
        renderValue: (selecteds) => selecteds.map((item) => item.label).join("،"),
        error: !!error,
        labelWidth,
        IconComponent: KeyboardArrowDownIcon
    };

    // Todo: the label is over selected items after unfocusing
    return (
        <FormControl fullWidth variant='outlined'>
            <InputLabel htmlFor={name} ref={inputLabel} className={classes.label}>
                {label}
            </InputLabel>
            <CustomizedSelect multiple {...attributes} {...rest}>
                {options.map((option) => (
                    <CustomizedMenuItem key={option.value} value={option}>
                        <Checkbox checked={value.indexOf(option) > -1} />
                        <CustomizedListItemText primary={option.label} disableTypography='true' />
                    </CustomizedMenuItem>
                ))}
            </CustomizedSelect>
            {attributes.error && (
                <FormHelperText id={`${name}-helper`} error={attributes.error}>
                    {error}
                </FormHelperText>
            )}
        </FormControl>
    );
}
