import React from "react";
import PropTypes from "prop-types";
import MaskedInput from "react-text-mask";
import InputLabel from "@material-ui/core/InputLabel";
import OutlinedInput from "@material-ui/core/OutlinedInput";
import FormControl from "@material-ui/core/FormControl";
import FormHelperText from "@material-ui/core/FormHelperText";
import { makeStyles, withStyles } from "@material-ui/core/styles";

const CustomizedOutlinedInput = withStyles({
    root: {
        fontSize: "0.9em"
    }
})(OutlinedInput);

function CustomMasked({ inputRef, ...other }) {
    const { mask, placeholderChar } = other;

    return (
        <MaskedInput
            {...other}
            ref={(ref) => {
                inputRef(ref ? ref.inputElement : null);
            }}
            mask={mask}
            placeholderChar={placeholderChar}
            showMask
        />
    );
}

CustomMasked.propTypes = {
    inputRef: PropTypes.func.isRequired
};

const useStyles = makeStyles((theme) => ({
    label: {
        fontSize: "0.9em"
    },
    error: {
        marginBottom: theme.spacing(2)
    }
}));
export default function DoranInput(props) {
    const { label, name, ltr = false, error, ...rest } = props;
    const classes = useStyles();

    const inputLabel = React.useRef(null);
    const [labelWidth, setLabelWidth] = React.useState(0);
    React.useEffect(() => {
        setLabelWidth(inputLabel.current.offsetWidth);
    }, []);

    const attributes = {
        id: name,
        name: name,
        error: !!error,
        style: ltr ? { direction: "ltr" } : null,
        "aria-describedby": `${name}-helper`,
        labelWidth
    };

    const helperAttrs = {
        className: attributes.error ? classes.error : null,
        id: attributes["aria-describedby"],
        error: attributes.error
    };

    const { mask } = rest;
    function renderInput(mask) {
        if (mask) {
            return (
                <CustomizedOutlinedInput
                    {...attributes}
                    {...rest}
                    inputComponent={CustomMasked}
                    inputProps={{
                        mask: mask
                    }}
                />
            );
        } else {
            return <CustomizedOutlinedInput {...attributes} {...rest} />;
        }
    }

    return (
        <FormControl fullWidth variant='outlined'>
            <InputLabel htmlFor={name} ref={inputLabel} className={classes.label}>
                {label}
            </InputLabel>
            {renderInput(mask)}
            {error && <FormHelperText {...helperAttrs}>{error}</FormHelperText>}
        </FormControl>
    );
}

DoranInput.propTypes = {
    label: PropTypes.string.isRequired,
    name: PropTypes.string.isRequired
};
