import React from "react";
import FormControl from "@material-ui/core/FormControl";
import InputLabel from "@material-ui/core/InputLabel";
import FormHelperText from "@material-ui/core/FormHelperText";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import KeyboardArrowDownIcon from "@material-ui/icons/KeyboardArrowDown";
import { makeStyles, withStyles } from "@material-ui/core/styles";

const CustomizedSelect = withStyles({
    root: {
        padding: "10px 14px",
        height: "32px",
        fontSize: "0.9rem",
        lineHeight: "2.785em"
    }
})(Select);

const CustomizedMenuItem = withStyles({
    root: {
        fontSize: "0.9rem"
    }
})(MenuItem);

const useStyles = makeStyles(() => ({
    label: {
        fontSize: "0.9rem"
    }
}));
export default function DoranSelect(props) {
    const { label, name, value, options, onChange, error, ...rest } = props;
    const classes = useStyles();

    const inputLabel = React.useRef(null);
    const [labelWidth, setLabelWidth] = React.useState(0);
    React.useEffect(() => {
        setLabelWidth(inputLabel.current.offsetWidth);
    }, []);

    const attributes = {
        renderValue: (select) => select.label,
        value: value || "",
        onChange: (event, child) => onChange(event, name, child.props.value),
        error: !!error,
        labelWidth,
        IconComponent: KeyboardArrowDownIcon
    };

    return (
        <FormControl fullWidth variant='outlined' className={classes.formControl}>
            <InputLabel htmlFor={name} ref={inputLabel} className={classes.label}>
                {label}
            </InputLabel>
            <CustomizedSelect {...attributes} {...rest}>
                {options.map((option) => (
                    <CustomizedMenuItem key={option.value} value={option}>
                        {option.label}
                    </CustomizedMenuItem>
                ))}
            </CustomizedSelect>

            {attributes.error && (
                <FormHelperText id={`${name}-helper`} error={attributes.error}>
                    {error}
                </FormHelperText>
            )}
        </FormControl>
    );
}
