import React from "react";
import TablePagination from "@material-ui/core/TablePagination";
import TableFooter from "@material-ui/core/TableFooter";
import TableRow from "@material-ui/core/TableRow";
import DoranPaginationActions from "./doran-pagination-actions";

const rowsPerPageOptions = [10, 20, 30, 40, 50];

export default function DoranPagination(props) {
    const { colSpan, totalCount, pageSize, pageNumber, pageSizeSelectable = false } = props;
    const { onChangePageClick, onChangeRowsPerPageClick } = props;
    const { rowsPerPageLabel, displayedRowsFormatter } = props;

    return (
        <TableFooter>
            <TableRow>
                <TablePagination
                    rowsPerPageOptions={pageSizeSelectable ? rowsPerPageOptions : []}
                    rowsPerPage={pageSize}
                    count={totalCount}
                    page={pageNumber - 1}
                    colSpan={colSpan}
                    onChangePage={onChangePageClick}
                    onChangeRowsPerPage={onChangeRowsPerPageClick}
                    labelRowsPerPage={rowsPerPageLabel}
                    labelDisplayedRows={displayedRowsFormatter}
                    ActionsComponent={DoranPaginationActions}
                />
            </TableRow>
        </TableFooter>
    );
}
