import React, { useState } from "react";
import Leaflet from "leaflet";
import { Map, TileLayer, Marker } from "react-leaflet";

delete Leaflet.Icon.Default.prototype._getIconUrl;

Leaflet.Icon.Default.mergeOptions({
    iconRetinaUrl: require("./leaflet/images/marker-icon-2x.png"),
    iconUrl: require("./leaflet/images/marker-icon.png"),
    shadowUrl: require("./leaflet/images/marker-shadow.png")
});

export default function DoranMap(props) {
    const { name, zoom, center, points: ps, single = false, onChange } = props;
    const [points, setPoints] = useState(ps);

    const handleAddClick = (event) => {
        const { lat, lng } = event.latlng;
        if (single) {
            setPoints([{ lat, lng }]);
        } else {
            points.push({ lat, lng });
        }
        onChange(name, points);
    };

    const handleRemoveClick = (index) => {
        points.splice(index, 1);
        onChange(name, points);
    };

    const style = {
        width: "100%",
        height: "100%",
        border: "1px solid rgba(0, 0, 0, 0.18)",
        borderRadius: "6px"
    };

    return (
        <Map center={center} zoom={zoom} style={style} onclick={handleAddClick}>
            <TileLayer
                attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                url='https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png'
            />
            {points.map((item, index) => (
                <Marker position={item} key={index} onClick={() => handleRemoveClick(index)} />
            ))}
        </Map>
    );
}
