import React from "react";
import DatePicker from "react-datepicker2";
import FormControl from "@material-ui/core/FormControl";
import jalali from "jalali-moment";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
    label: {
        fontSize: "13px"
    },
    input: {
        fontSize: "13px",
        direction: "ltr",
        padding: "25px 14px",
        height: "1.2875em",
        width: "100%",
        borderRadius: "4px",
        boxSizing: "border-box",
        border: "1px solid lightgrey",
        "&:hover": {
            borderColor: "black"
        },
        "&:focus": {
            borderWidth: "2px",
            borderColor: "#3f51b5",
            outlineWidth: "0px"
        }
    }
}));
const DoranDatePicker = (props) => {
    const { value, name, placeholder, format, gregorian, onChange } = props;
    const classes = useStyles();

    const attrs = {
        timePicker: false,
        isGregorian: gregorian,
        value: value ? jalali.from(value, format) : "",
        inputJalaaliFormat: "jYYYY-jMM-jDD",
        showTodayButton: false,
        className: classes.input,
        placeholder,
        onChange: (changedValue) => {
            if (value.toString() === changedValue.toString()) return;

            onChange({ currentTarget: { value: changedValue, name } });
        }
    };

    return (
        <FormControl fullWidth variant='outlined'>
            <DatePicker {...attrs} />
        </FormControl>
    );
};

export default DoranDatePicker;
