import React from "react";
import Button from "@material-ui/core/Button";
import Grid from "@material-ui/core/Grid";
import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
    dialogTitle: {
        padding: 0
    },
    appBar: {
        position: "relative"
    },
    title: {
        marginLeft: theme.spacing(2),
        flex: 1
    },
    form: {
        display: "flex",
        flexDirection: "row"
    }
}));
export default function DoranOperationPanel(props) {
    const classes = useStyles();
    const { children, open, size = "sm", validate, onSave, onClose } = props;
    const { title, closeText, saveText } = props;

    const handleSubmit = (event) => {
        event.preventDefault();
    };

    return (
        <Dialog fullWidth maxWidth={size} open={open} onClose={onClose} aria-labelledby='dialog-title'>
            <DialogTitle id='dialog-title' className={classes.dialogTitle}>
                <AppBar className={classes.appBar}>
                    <Toolbar>
                        <Typography variant='h6' className={classes.title}>
                            {title}
                        </Typography>
                    </Toolbar>
                </AppBar>
            </DialogTitle>
            <DialogContent dividers>
                <form className={classes.form} autoComplete='off' noValidate onSubmit={handleSubmit}>
                    <Grid container spacing={1}>
                        {children}
                    </Grid>
                </form>
            </DialogContent>
            <DialogActions>
                <Button color='secondary' onClick={onClose}>
                    {closeText}
                </Button>
                {onSave && (
                    <Button type='submit' variant='contained' color='primary' onClick={onSave} disabled={!!validate()}>
                        {saveText}
                    </Button>
                )}
            </DialogActions>
        </Dialog>
    );
}
