import React from "react";
import TableCell from "@material-ui/core/TableCell";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import { makeStyles } from "@material-ui/core/styles";

const styles = makeStyles({
    th: {
        fontSize: "0.95rem",
        fontWeight: "bold",
        whiteSpace: "nowrap"
    }
});
export default function DoranHeader(props) {
    const classes = styles();
    const { columns } = props;

    return (
        <TableHead>
            <TableRow>
                {columns.map((column) => (
                    <TableCell key={column.key} className={classes.th} style={{ width: column.width }}>
                        {column.label}
                    </TableCell>
                ))}
            </TableRow>
        </TableHead>
    );
}
