import React from "react";
import FormControl from "@material-ui/core/FormControl";
import InputLabel from "@material-ui/core/InputLabel";
import OutlinedInput from "@material-ui/core/OutlinedInput";
import Select from "@material-ui/core/Select";
import IconButton from "@material-ui/core/IconButton";
import MenuItem from "@material-ui/core/MenuItem";
import DeleteForeverIcon from "@material-ui/icons/DeleteForever";
import KeyboardArrowDownIcon from "@material-ui/icons/KeyboardArrowDown";
import { makeStyles, withStyles } from "@material-ui/core/styles";

const CustomizedSelect = withStyles({
    root: {
        padding: "10px 14px",
        height: "32px",
        fontSize: "0.8rem",
        lineHeight: "2.785em"
    }
})(Select);

const CustomizedMenuItem = withStyles({
    root: {
        fontSize: "0.8rem"
    }
})(MenuItem);

const CustomizedOutlinedInput = withStyles({
    root: {
        fontSize: "0.8rem"
    }
})(OutlinedInput);

const useStyles = makeStyles((theme) => ({
    root: {
        display: "flex",
        justifyContent: ""
    },
    select: {
        flexGrow: 1,
        marginRight: theme.spacing(0.5)
    },
    input: {
        flexGrow: 3
    },
    buttonLabel: {
        flexGrow: 0,
        alignSelf: "center"
    },
    button: {
        color: "red"
    },
    label: {
        fontSize: theme.spacing(1.6)
    }
}));
export default function DoranConatctInput(props) {
    // TODO error and disabled will be added
    const { itemKey, value, onChange, onRemove } = props;
    const { selectName, selectLabel, options } = props;
    const { inputName, inputLabel } = props;

    const classes = useStyles();

    const selectInputLabel = React.useRef(null);
    const [selectLabelWidth, setSelectLabelWidth] = React.useState(0);
    React.useEffect(() => {
        setSelectLabelWidth(selectInputLabel.current.offsetWidth);
    }, []);

    const inputInputLabel = React.useRef(null);
    const [inputLabelWidth, setInputLabelWidth] = React.useState(0);
    React.useEffect(() => {
        setInputLabelWidth(inputInputLabel.current.offsetWidth);
    }, []);

    const handleSelectChange = (event, child) => {
        event.preventDefault();

        const item = child.props.value;
        const newValue = Object.assign(value, { type: options.find((e) => e.value === item.value) });
        onChange(itemKey, newValue);
    };

    const handleInputChange = (event) => {
        event.preventDefault();

        const newValue = Object.assign(value, { value: event.target.value });
        onChange(itemKey, newValue);
    };

    const selectAttributes = {
        renderValue: (select) => select.label,
        value: value.type || "",
        labelWidth: selectLabelWidth,
        IconComponent: KeyboardArrowDownIcon,
        onChange: (event, child) => handleSelectChange(event, child)
    };

    const inputAttributes = {
        id: inputName,
        name: inputName,
        value: value.value || "",
        labelWidth: inputLabelWidth,
        onChange: (event) => handleInputChange(event)
    };

    return (
        <div className={classes.root}>
            <FormControl variant='outlined' className={classes.select}>
                <InputLabel htmlFor={selectName} ref={selectInputLabel} className={classes.label}>
                    {selectLabel}
                </InputLabel>
                <CustomizedSelect {...selectAttributes}>
                    {options.map((option) => (
                        <CustomizedMenuItem key={option.value} value={option}>
                            {option.label}
                        </CustomizedMenuItem>
                    ))}
                </CustomizedSelect>
            </FormControl>
            <FormControl variant='outlined' className={classes.input}>
                <InputLabel htmlFor={inputName} ref={inputInputLabel} className={classes.label}>
                    {inputLabel}
                </InputLabel>
                <CustomizedOutlinedInput {...inputAttributes} />
            </FormControl>
            <label className={classes.buttonLabel}>
                <IconButton component='span' className={classes.button} onClick={() => onRemove(itemKey)}>
                    <DeleteForeverIcon />
                </IconButton>
            </label>
        </div>
    );
}
