import React, { Component } from "react";
import Joi from "joi-browser";
import DoranInput from "../input/doran-input";
import DoranCheckbox from "../checkbox/doran-checkbox";
import DoranButton from "../button/doran-button";
import DoranMultiSelect from "../select/doran-multi-select";
import DoranMultiTagSelect from "../select/doran-multi-tag-select";
import DoranSelect from "../select/doran-select";
import DoranAutocomplete from "../autocomplete/doran-autocomplete";
import DoranMultiSelectAutocomplete from "../autocomplete/doran-multi-select-autocomplete";
import DoranImageInput from "../input/doran-image-input";
import DoranContact from "../contact/doran-contact";
import DoranDatePicker from "../datepicker/doran-date-picker";
import DoranMap from "../map/doran-map";
import DoranEditor from "../editor/doran-editor";
import DoranBirthDate from "../datepicker/doran-birth-date";

class DoranForm extends Component {
    state = {
        data: {},
        errors: {},
        validate: false
    };

    validate = () => {
        const options = { abortEarly: false };
        const { error } = Joi.validate(this.state.data, this.schema, options);
        if (!error) return null;

        const errors = {};
        for (const item of error.details) errors[item.path[0]] = item.message;
        return errors;
    };

    validateProperty = ({ name, value }) => {
        const obj = { [name]: value };
        const schema = { [name]: this.schema[name] };
        const { error } = Joi.validate(obj, schema);
        return error ? error.details[0].message : null;
    };

    handleTextChange = ({ currentTarget: input }) => {
        const errors = { ...this.state.errors };

        if (this.state.validate) {
            const errorMessage = this.validateProperty(input);
            if (errorMessage) errors[input.name] = errorMessage;
            else delete errors[input.name];
        }

        const data = { ...this.state.data };
        data[input.name] = input.value;

        this.setState({ data, errors });
    };

    handleEditorChange = (name, value) => {
        const errors = { ...this.state.errors };

        if (this.state.validate) {
            const errorMessage = this.validateProperty({ name, value });
            if (errorMessage) errors[name] = errorMessage;
            else delete errors[name];
        }

        const data = { ...this.state.data };
        data[name] = value;

        this.setState({ data, errors });
    };

    handleMapChange = (name, points) => {
        const errors = { ...this.state.errors };

        if (this.state.validate) {
            const errorMessage = this.validateProperty({ name, value: points });
            if (errorMessage) errors[name] = errorMessage;
            else delete errors[name];
        }

        const data = { ...this.state.data };
        data[name] = points;

        this.setState({ data, errors });
    };

    handleCheckboxChange = ({ currentTarget: input }) => {
        const errors = { ...this.state.errors };

        if (this.state.validate) {
            const errorMessage = this.validateProperty(input);
            if (errorMessage) errors[input.name] = errorMessage;
            else delete errors[input.name];
        }

        const data = { ...this.state.data };
        data[input.name] = input.checked;

        this.setState({ data, errors });
    };

    handleMultiSelectChange = (event, name, value) => {
        const errors = { ...this.state.errors };

        if (this.state.validate) {
            const errorMessage = this.validateProperty({ name, value: [value] });
            if (errorMessage) errors[name] = errorMessage;
            else delete errors[name];
        }

        const data = { ...this.state.data };
        const index = data[name].indexOf(value);
        if (index === -1) {
            data[name].splice(data[name].length - 1, 0, value);
        } else {
            data[name].splice(index, 1);
        }

        this.setState({ data, errors });
    };

    handleSelectChange = (event, name, value) => {
        const errors = { ...this.state.errors };

        if (this.state.validate) {
            const errorMessage = this.validateProperty({ name, value });
            if (errorMessage) errors[name] = errorMessage;
            else delete errors[name];
        }

        const data = { ...this.state.data };
        data[name] = value;

        this.setState({ data, errors });
    };

    handleBirthDateChange = (name, value) => {
        const errors = { ...this.state.errors };

        if (this.state.validate) {
            const errorMessage = this.validateProperty({ name, value });
            if (errorMessage) errors[name] = errorMessage;
            else delete errors[name];
        }

        const data = { ...this.state.data };
        data[name] = value;

        this.setState({ data, errors });
    };

    handleAutoCompleteChange = (event, name, value, onChange) => {
        const errors = { ...this.state.errors };

        if (this.state.validate) {
            const errorMessage = this.validateProperty({ name, value });
            if (errorMessage) errors[name] = errorMessage;
            else delete errors[name];
        }

        const data = { ...this.state.data };
        data[name] = value;
        this.setState({ data, errors });

        if (onChange) onChange(value);
    };

    handleMultiSelectAutoCompleteChange = (_, name, value, onChange) => {
        const errors = { ...this.state.errors };

        if (this.state.validate) {
            const errorMessage = this.validateProperty({ name, value });
            if (errorMessage) errors[name] = errorMessage;
            else delete errors[name];
        }

        const data = { ...this.state.data };
        data[name] = value;
        this.setState({ data, errors });

        if (onChange) onChange(value);
    };

    handleImageChange = (name, value) => {
        const errors = { ...this.state.errors };

        if (this.state.validate) {
            const errorMessage = this.validateProperty({ name, value });
            if (errorMessage) errors[name] = errorMessage;
            else delete errors[name];
        }

        const data = { ...this.state.data };
        data[name] = value;
        this.setState({ data, errors });

        // if (onChange) onChange(value);
    };

    renderSelect(attr) {
        const { name, label, options, disabled = false } = attr;
        const { data, errors } = this.state;
        const attributes = {
            name,
            value: data[name],
            label,
            options,
            onChange: this.handleSelectChange,
            disabled: disabled
        };

        if (this.state.validate) {
            attributes.error = errors[name];
        }

        return <DoranSelect {...attributes} />;
    }

    renderMultiSelect(info) {
        const { data, errors } = this.state;
        const { label, name, options } = info;
        const attributes = {
            name,
            value: data[name],
            label,
            options,
            onChange: this.handleMultiSelectChange
        };

        if (this.state.validate) {
            attributes.error = errors[name];
        }

        return <DoranMultiSelect {...attributes} />;
    }

    renderMultiTagSelect(label, name, options) {
        const { data, errors } = this.state;
        const attributes = {
            name,
            value: data[name],
            label,
            options,
            onChange: this.handleMultiSelectChange
        };

        if (this.state.validate) {
            attributes.error = errors[name];
        }

        return <DoranMultiTagSelect {...attributes} />;
    }

    renderAutocomplete(props) {
        const { label, name, queryName, disabled = false } = props;
        const { serviceCallback, convertCallback, onChangeCallback, defaultCallbackObject } = props;
        const { data, errors } = this.state;
        const attributes = {
            label,
            name,
            value: data[name],
            queryName,
            serviceCallback,
            convertCallback,
            defaultCallbackObject,
            onChangeCallback: (event, name, value) =>
                this.handleAutoCompleteChange(event, name, value, onChangeCallback),
            disabled
        };

        if (this.state.validate) {
            attributes.error = errors[name];
        }

        return <DoranAutocomplete {...attributes} />;
    }

    renderMultiSelectAutocomplete(props) {
        const { label, name, queryName, disabled = false } = props;
        const { serviceCallback, convertCallback, onChangeCallback, defaultCallbackObject } = props;
        const { data, errors } = this.state;
        const attributes = {
            label,
            name,
            value: data[name],
            queryName,
            serviceCallback,
            convertCallback,
            defaultCallbackObject,
            onChangeCallback: (event, name, value) =>
                this.handleAutoCompleteChange(event, name, value, onChangeCallback),
            disabled
        };

        if (this.state.validate) {
            attributes.error = errors[name];
        }

        return <DoranMultiSelectAutocomplete {...attributes} />;
    }

    renderContact(attrs) {
        const { name, title, addText, inputLabelText, selectLabelText, emptyText } = attrs;
        const { contactTypes } = attrs;
        const { data } = this.state;
        const attributes = {
            name,
            contactTypes,
            value: data[name],
            title,
            addText,
            inputLabelText,
            selectLabelText,
            emptyText,
            onChange: (value) => this.handleContactChange(name, value)
        };

        return <DoranContact {...attributes} />;
    }

    renderBirthDate(attrs) {
        const { name, dayName, dayLabel } = attrs;
        const { monthName, monthLabel, monthOptions } = attrs;
        const { yearName, yearLabel } = attrs;
        const { data } = this.state;
        const attributes = {
            name,
            dayName,
            dayLabel,
            monthName,
            monthLabel,
            monthOptions,
            yearName,
            yearLabel,
            value: data[name],
            onChange: (value) => this.handleBirthDateChange(name, value)
        };

        return <DoranBirthDate {...attributes} />;
    }

    renderInput(attr) {
        const { name, label, mask, ltr = false, type = "text" } = attr;
        const { data, errors } = this.state;
        const attributes = {
            name,
            type,
            value: data[name],
            label,
            ltr,
            onChange: this.handleTextChange,
            mask
        };

        if (this.state.validate) {
            attributes.error = errors[name];
        }

        return <DoranInput {...attributes} />;
    }

    renderCheckbox(attr) {
        const { name, label, placement = "end" } = attr;
        const { data, errors } = this.state;
        const attributes = {
            name,
            value: data[name],
            label,
            placement,
            onChange: this.handleCheckboxChange
        };

        if (this.state.validate) {
            attributes.error = errors[name];
        }

        return <DoranCheckbox {...attributes} />;
    }

    renderTextArea(attr) {
        const { name, label, ltr = false, rows = 5 } = attr;
        const { data, errors } = this.state;
        const attributes = {
            type: "text",
            multiline: true,
            name,
            value: data[name],
            label,
            ltr,
            rows,
            onChange: this.handleTextChange
        };

        if (this.state.validate) {
            attributes.error = errors[name];
        }

        return <DoranInput {...attributes} />;
    }

    renderImageInput(attr) {
        const { name, emptyImage, removeText, uploadText, minHeight } = attr;
        const { data, errors } = this.state;
        const attributes = {
            name,
            value: data[name],
            emptyImage,
            removeText,
            uploadText,
            minHeight,
            onChange: (name, value) => this.handleImageChange(name, value)
        };

        if (this.state.validate) {
            attributes.error = errors[name];
        }

        return <DoranImageInput {...attributes} />;
    }

    renderDatePicker(attr) {
        const { name, label, gregorian = false, placeholder, format = "jYYYY-jMM-jDD" } = attr;
        const { data, errors } = this.state;
        const attributes = {
            name,
            value: data[name],
            label,
            gregorian,
            placeholder,
            format,
            onChange: this.handleTextChange
        };

        if (this.state.validate) {
            attributes.error = errors[name];
        }

        return <DoranDatePicker {...attributes} />;
    }

    renderButton(attr) {
        const { label, type = "submit" } = attr;
        const attributes = {
            type,
            label,
            variant: "contained",
            color: "primary",
            size: "large",
            disabled: this.validate() ? true : false
        };
        return <DoranButton fullWidth {...attributes} />;
    }

    renderMap(attrs) {
        const { name, center, zoom, single } = attrs;
        const { data } = this.state;
        const attributes = {
            name,
            center,
            zoom,
            single,
            points: data[name],
            onChange: (name, value) => this.handleMapChange(name, value)
        };

        return <DoranMap {...attributes} />;
    }

    renderEditor(attrs) {
        const { name, label } = attrs;
        const { data } = this.state;
        const attributes = {
            name,
            label,
            value: data[name],
            onChange: (name, value) => this.handleEditorChange(name, value)
        };

        return <DoranEditor {...attributes} />;
    }
}

export default DoranForm;
