import React from "react";
import throttle from "lodash/throttle";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import Autocomplete from "@material-ui/lab/Autocomplete";
import CircularProgress from "@material-ui/core/CircularProgress";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles(() => ({
    input: {
        fontSize: "0.8rem"
    },
    optionLabel: {
        fontSize: "0.8rem",
        fontWeight: "bold"
    },
    optionSecondaryLabel: {
        fontSize: "0.7rem"
    }
}));

export default function DoranAutocomplete(props) {
    const classes = useStyles();

    const { label, name, value, disabled, queryName } = props;
    const { serviceCallback, convertCallback, onChangeCallback, defaultCallbackObject } = props;

    const [inputValue, setInputValue] = React.useState(null);
    const [options, setOptions] = React.useState([]);
    const [loading, setLoading] = React.useState(false);

    const fetch = React.useMemo(
        () =>
            throttle(async ({ input }, callback) => {
                let searchObject = { [queryName]: input };

                if (defaultCallbackObject) {
                    searchObject = Object.assign(defaultCallbackObject, { [queryName]: input });
                }

                const data = await serviceCallback(searchObject);
                const items = convertCallback(data);
                callback(items);
            }, 200),
        [queryName, convertCallback, serviceCallback, defaultCallbackObject]
    );

    React.useEffect(() => {
        let active = true;

        if (!inputValue) {
            setOptions([]);
            return;
        }

        fetch({ input: inputValue }, (results) => {
            if (active) {
                setOptions(results || []);
            }
        });

        return () => {
            active = false;
        };
    }, [inputValue, fetch]);

    const autocompleteAttributes = {
        getOptionLabel: (option) => option.label,
        options,
        value,
        // disabled,
        disableClearable: disabled,
        // defaultValue: value,
        onChange: (event, newValue) => onChangeCallback(event, name, newValue),
        onOpen: () => setLoading(true),
        onClose: () => setLoading(false)
    };

    const inputAttributes = {
        label,
        variant: "outlined",
        disabled,
        // helperText: error,
        onChange: (event) => setInputValue(event.target.value)
    };

    return (
        <React.Fragment>
            <Autocomplete
                autoComplete
                includeInputInList
                freeSolo
                {...autocompleteAttributes}
                renderInput={(params) => (
                    // Todo: can not able to clear simple entered text if no option is selected
                    <TextField
                        // error
                        {...inputAttributes}
                        {...params}
                        fullWidth
                        InputLabelProps={{ className: classes.input }}
                        InputProps={{
                            ...params.InputProps,
                            className: classes.input,
                            endAdornment: (
                                <React.Fragment>
                                    {loading ? <CircularProgress color='inherit' size={20} /> : null}
                                    {loading || params.InputProps.endAdornment}
                                </React.Fragment>
                            )
                        }}
                    />
                )}
                renderOption={(option) => {
                    const secondaryAttrs = {
                        variant: "body2",
                        color: "textSecondary",
                        className: classes.optionSecondaryLabel
                    };

                    return (
                        <Grid container alignItems='center'>
                            <Grid item xs>
                                <span key={option.value} className={classes.optionLabel}>
                                    {option.label}
                                </span>

                                {option.description && (
                                    <Typography {...secondaryAttrs}>{option.description}</Typography>
                                )}
                            </Grid>
                        </Grid>
                    );
                }}
                filterOptions={(options, state) => {
                    options.forEach((option) => {
                        if (value && value.length) {
                            var length = value.filter((e) => e.value === option.value).length;
                            if (length > 0) {
                                option.disabled = true;
                            }
                        } else if (value && value.value === option.value) {
                            option.disabled = true;
                        }
                    });

                    return options.filter((e) => !e.disabled);
                }}
            />
        </React.Fragment>
    );
}
