import React from "react";
import PropTypes from "prop-types";
import { makeStyles } from "@material-ui/core/styles";
import TreeItem from "@material-ui/lab/TreeItem";
import Typography from "@material-ui/core/Typography";

const useTreeItemStyles = makeStyles((theme) => ({
    root: {},
    content: {
        color: theme.palette.text.secondary,
        borderTopRightRadius: theme.spacing(2),
        borderBottomRightRadius: theme.spacing(2),
        paddingRight: theme.spacing(1),
        fontWeight: theme.typography.fontWeightMedium,
        "$expanded > &": {
            fontWeight: theme.typography.fontWeightRegular
        }
    },
    group: {
        marginLeft: 0,
        "& $content": {
            paddingLeft: theme.spacing(2)
        }
    },
    expanded: {},
    selected: {},
    label: {
        fontWeight: "inherit",
        color: "inherit",
        borderRadius: "4px"
    },
    labelRoot: {
        display: "flex",
        alignItems: "center"
    },
    labelIcon: {
        marginRight: theme.spacing(1)
    },
    labelText: {
        fontWeight: "inherit",
        flexGrow: 1
    }
}));

function DoranTreeViewItem(props) {
    const classes = useTreeItemStyles();
    const { labelText, labelIcon: LabelIcon, color, bgColor, ...other } = props;
    const { operation } = props;

    return (
        <TreeItem
            label={
                <div className={classes.labelRoot}>
                    {LabelIcon && <LabelIcon color='inherit' className={classes.labelIcon} />}
                    <Typography variant='body2' className={classes.labelText}>
                        {labelText}
                    </Typography>
                    {operation}
                </div>
            }
            style={{
                "--tree-view-color": color,
                "--tree-view-bg-color": bgColor
            }}
            classes={{
                root: classes.root,
                content: classes.content,
                expanded: classes.expanded,
                selected: classes.selected,
                group: classes.group,
                label: classes.label
            }}
            {...other}
        />
    );
}

DoranTreeViewItem.propTypes = {
    bgColor: PropTypes.string,
    color: PropTypes.string,
    labelIcon: PropTypes.elementType.isRequired,
    labelInfo: PropTypes.string,
    labelText: PropTypes.oneOfType([PropTypes.string, PropTypes.object]).isRequired
};

export default DoranTreeViewItem;
