import React from "react";
import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles({
    button: {
        textAlign: "right",
        marginTop: "16px"
    }
});
export default function DoranButton(props) {
    const { label, type = "submit", ...rest } = props;
    const classes = useStyles();
    return (
        <Button type={type} className={classes.button} {...rest}>
            {label}
        </Button>
    );
}
