import React from "react";
import FormControl from "@material-ui/core/FormControl";
import InputLabel from "@material-ui/core/InputLabel";
import OutlinedInput from "@material-ui/core/OutlinedInput";
import Select from "@material-ui/core/Select";
import IconButton from "@material-ui/core/IconButton";
import MenuItem from "@material-ui/core/MenuItem";
import DeleteForeverIcon from "@material-ui/icons/DeleteForever";
import KeyboardArrowDownIcon from "@material-ui/icons/KeyboardArrowDown";
import { makeStyles, withStyles } from "@material-ui/core/styles";

const CustomizedSelect = withStyles({
    root: {
        padding: "10px 14px",
        height: "32px",
        fontSize: "0.8rem",
        lineHeight: "2.785em"
    }
})(Select);

const CustomizedMenuItem = withStyles({
    root: {
        fontSize: "0.8rem"
    }
})(MenuItem);

const CustomizedOutlinedInput = withStyles({
    root: {
        fontSize: "0.8rem"
    }
})(OutlinedInput);

const useStyles = makeStyles((theme) => ({
    root: {
        display: "flex",
        justifyContent: ""
    },
    select: {
        flexGrow: 1,
        marginRight: theme.spacing(0.5)
    },
    input: {
        flexGrow: 3
    },
    buttonLabel: {
        flexGrow: 0,
        alignSelf: "center"
    },
    button: {
        color: "red"
    },
    label: {
        fontSize: theme.spacing(1.6)
    }
}));
export default function DoranBithDate(props) {
    // TODO error and disabled will be added
    const { value, onChange } = props;
    const { monthName, monthLabel, monthOptions } = props;
    const { dayName, dayLabel } = props;
    const { yearName, yearLabel } = props;

    const classes = useStyles();

    const selectInputLabel = React.useRef(null);
    const [selectLabelWidth, setSelectLabelWidth] = React.useState(0);
    React.useEffect(() => {
        setSelectLabelWidth(selectInputLabel.current.offsetWidth);
    }, []);

    const inputInputLabel = React.useRef(null);
    const [inputLabelWidth, setInputLabelWidth] = React.useState(0);
    React.useEffect(() => {
        setInputLabelWidth(inputInputLabel.current.offsetWidth);
    }, []);

    const handleSelectChange = (event, child) => {
        event.preventDefault();

        const item = child.props.value;
        const newValue = monthOptions.find((e) => e.value === item.value);
        console.log(newValue);
        // onChange(itemKey, newValue);
    };

    const handleDayChange = (event) => {
        event.preventDefault();
        console.log(event.target.value);

        // const newValue = Object.assign(value, { value: event.target.value });
        // onChange(itemKey, newValue);
    };

    const handleYearChange = (event) => {
        event.preventDefault();
        console.log(event.target.value);

        // const newValue = Object.assign(value, { value: event.target.value });
        // onChange(itemKey, newValue);
    };

    const selectAttributes = {
        renderValue: (select) => select.label,
        // value: value.type || "",
        labelWidth: selectLabelWidth,
        IconComponent: KeyboardArrowDownIcon,
        onChange: (event, child) => handleSelectChange(event, child)
    };

    const dayInputAttributes = {
        type: "number",
        inputProps: {
            min: 1,
            max: 31
        },
        id: dayName,
        name: dayName,
        // value: dayMinMax,
        labelWidth: inputLabelWidth,
        onChange: (event) => handleDayChange(event)
    };

    const yearInputAttributes = {
        type: "number",
        min: 1300,
        max: 1400,
        id: yearName,
        name: yearName,
        // value: value.value || "",
        labelWidth: inputLabelWidth,
        onChange: (event) => handleYearChange(event)
    };

    console.log(dayInputAttributes);

    return (
        <div className={classes.root}>
            <FormControl variant='outlined' className={classes.input}>
                <InputLabel htmlFor={dayName} ref={inputInputLabel} className={classes.label}>
                    {dayLabel}
                </InputLabel>
                <CustomizedOutlinedInput {...dayInputAttributes} />
            </FormControl>
            <FormControl variant='outlined' className={classes.select}>
                <InputLabel htmlFor={monthName} ref={selectInputLabel} className={classes.label}>
                    {monthLabel}
                </InputLabel>
                <CustomizedSelect {...selectAttributes}>
                    {monthOptions.map((option) => (
                        <CustomizedMenuItem key={option.value} value={option}>
                            {option.label}
                        </CustomizedMenuItem>
                    ))}
                </CustomizedSelect>
            </FormControl>
            <FormControl variant='outlined' className={classes.input}>
                <InputLabel htmlFor={yearName} ref={inputInputLabel} className={classes.label}>
                    {yearLabel}
                </InputLabel>
                <CustomizedOutlinedInput {...yearInputAttributes} />
            </FormControl>
        </div>
    );
}
